import {
  REQUEST_POSTS_PROXYS,
  RECEIVE_POSTS_PROXYS
} from './actions'

function posts(
  state = {
    isFetching: false,
    didInvalidate: false,
    items: []
  },
  action
) {
  switch (action.type) {

    case REQUEST_POSTS_PROXYS:
      return Object.assign({}, state, {
        isFetching: true,
        didInvalidate: false
      })
    case RECEIVE_POSTS_PROXYS:
      return Object.assign({}, state, {
        isFetching: false,
        didInvalidate: false,
        items: action.posts,
        lastUpdated: action.receivedAt
      })
    default:
      return state
  }
}

function proxysStore(state = {}, action) {
  console.log(action);
  switch (action.type) {
    case RECEIVE_POSTS_PROXYS:
    case REQUEST_POSTS_PROXYS:
      return Object.assign({}, state, {
        ...posts(state, action)
      })
    default:
      return state
  }
}

export default proxysStore