import {
  REQUEST_POSTS_PAGES,
  RECEIVE_POSTS_PAGES
} from './actions'

function posts(
  state = {
    isFetching: false,
    didInvalidate: false,
    pages: []
  },
  action
) {
  switch (action.type) {
    case REQUEST_POSTS_PAGES:
      return Object.assign({}, state, {
        isFetching: true,
        didInvalidate: false
      })
    case RECEIVE_POSTS_PAGES:
      return Object.assign({}, state, {
        isFetching: false,
        didInvalidate: false,
        pages: action.posts,
        lastUpdated: action.receivedAt
      })
    default:
      return state
  }
}

function pagesStore(state={}, action) {
  switch (action.type) {
    case REQUEST_POSTS_PAGES:
    case RECEIVE_POSTS_PAGES:
      return Object.assign({}, state, {
        ...posts(state, action)
      })
    default:
      return state
  }
}

export default pagesStore