import axios from 'axios'

export const REQUEST_POSTS_PAGES = 'REQUEST_POSTS_PAGES'
function requestPosts() {
  return {
    type: REQUEST_POSTS_PAGES,
  }
}

export const RECEIVE_POSTS_PAGES = 'RECEIVE_POSTS_PAGES'
function receivePosts(json) {
  return {
    type: RECEIVE_POSTS_PAGES,
    posts: json.data.map(child => child),
    receivedAt: Date.now()
  }
}

function fetchPosts(params) {
  return dispatch => {
    dispatch(requestPosts(params))
    return axios({
      method: 'get',
      url: '/CLI/WEB_CONFIG/pages',
    }).then(json => dispatch(receivePosts(json.data)))
  }
}

// params请求参数
function shouldFetchPosts(state) {
  const posts = state.pagesStore;
  
  if (!Object.keys(posts).length) {
    return true
  } else if (posts.isFetching) {
    return false
  } else {
    return posts.didInvalidate
  }
}

export function fetchPostsIfNeeded(params) {
  // 注意这个函数也接收了 getState() 方法
  // 它让你选择接下来 dispatch 什么。

  // 当缓存的值是可用时，
  // 减少网络请求很有用。
  return (dispatch, getState) => {
    if (shouldFetchPosts(getState())) {
      // 在 thunk 里 dispatch 另一个 thunk！
      return dispatch(fetchPosts(params))
    } else {
      // 告诉调用代码不需要再等待。
      console.log("已获取数据，无需重新请求")
      return Promise.resolve()
    }
  }
}