import axios from 'axios'

export const REQUEST_POSTS_PROXYS = 'REQUEST_POSTS_PROXYS'
function requestPosts() {
  return {
    type: REQUEST_POSTS_PROXYS,
  }
}

export const RECEIVE_POSTS_PROXYS = 'RECEIVE_POSTS_PROXYS'
function receivePosts(json) {
  return {
    type: RECEIVE_POSTS_PROXYS,
    posts: json.data.map(child => child),
    receivedAt: Date.now()
  }
}

function fetchPosts(params) {
  return dispatch => {
    dispatch(requestPosts(params))
    return axios({
      method: 'get',
      url: '/CLI/WEB_CONFIG/proxys',
    }).then(json => dispatch(receivePosts(json.data)))
  }
}

function shouldFetchPosts(state) {
  const posts = state.proxysStore;
  console.log(posts);
  if (!Object.keys(posts).length) {
    return true
  } else if (posts.isFetching) {
    return false
  } else {
    return posts.didInvalidate
  }
}

export function fetchPostsIfNeeded(params, isReset=false) {
  // 注意这个函数也接收了 getState() 方法
  // 它让你选择接下来 dispatch 什么。

  // 当缓存的值是可用时，
  // 减少网络请求很有用。

  return (dispatch, getState) => {
    if (isReset || shouldFetchPosts(getState())) {
      // 在 thunk 里 dispatch 另一个 thunk！
      return dispatch(fetchPosts(params))
    } else {
      // 告诉调用代码不需要再等待。
      return Promise.resolve()
    }
  }
}
function addProxys(params) {
  return dispatch => {
    return axios({
      method: params.proxyId ? 'put' : "post",
      url: '/CLI/WEB_CONFIG/proxys',
      data: params
    }).then(json => json.data)
  }
}
export function addProxysFetch(params) {
  // 注意这个函数也接收了 getState() 方法
  // 它让你选择接下来 dispatch 什么。

  // 当缓存的值是可用时，
  // 减少网络请求很有用。

  return (dispatch) => {
    return dispatch(addProxys(params))
  }
}